const item = document.querySelectorAll('.theme');
const itemLen = item.length;

for ( let itemCount = 0; itemCount < itemLen; itemCount++) {
    item[itemCount].addEventListener('mouseover', slideOn, false);
    item[itemCount].addEventListener('mouseleave', slideOff, false);
}

function slideOn(){
    $(this).addClass('active');
}

function slideOff(){
    $(this).removeClass('active');
}